// import axios from 'axios';
import history from 'src/app/history';
class Auth {  
  // signup = () => {
    
  // };  

  // login = () => {
    
  // }  
  
  isAuthenticated = () => {
    if(localStorage.getItem('authdata') && JSON.parse(localStorage.getItem('authdata')).token){
      return true;
    } else {
      return false;
    }
  };  

  logout = () => {
    localStorage.removeItem('authdata');
    localStorage.removeItem('current_account');   
    history.replace('/'); 
  };

}



export default new Auth();