import React from 'react';
import { Route, withRouter, Redirect } from 'react-router-dom';
import { 
  P404,
} from 'src/utils/constants';


const PublicRoute = ({ component: Component, auth, location, ...rest }) => {   
  const authenticated = auth.isAuthenticated();
  if (authenticated && location.pathname !== P404) {    
    return <Redirect to='/' />;
  }  
  return <Route {...rest} render={(props) => <Component auth={auth} {...props} />} />;
};

export default withRouter(PublicRoute);
