import { all, call, put, takeLatest } from 'redux-saga/effects';
import * as generalActions from 'src/actions/general';
import { toast } from 'react-toastify';
import { 
  xxxApi,
} from 'src/requests/general';

export function* xxxFunction(action) {
  try {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: true })
    const response = yield call(xxxApi)    
    yield put({ type: generalActions.XXX_ACTION_SUCCESS, data: response.data})    
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    toast.success(`call(xxxApi) Done.`);
  } catch(e) {
    yield put({ type: generalActions.SHOW_MAIN_LOADER, data: false })
    yield put({ type: generalActions.XXX_ACTION_FAILED, error: e })
    toast.error(`call(xxxApi) Error.`)
  }
}

export default all([
  takeLatest(generalActions.XXX_ACTION_REQUEST, xxxFunction),  
]);
