import {
  SHOW_MAIN_LOADER,  
} from 'src/actions/general';

const initialState = {
  isLoading: false,  
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case SHOW_MAIN_LOADER:      
      return {
        ...state,
        isLoading: action.data,
      };
    
    default:
      return state;
  }
}
